.delivery__label {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.delivery__container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  @media #{$tablet} {
    flex-wrap: nowrap;
  }
}

.delivery__label_name,
.delivery__label_phone {
  width: 100%;

  @media #{$tablet} {
    width: calc(50% - 12px);
  }
}

.delivery__field {
  display: block;
  margin-top: 15px;
  padding: 16px;
  border: 1px solid $color-text-inactive;
  border-radius: 4px;
  font-size: 1rem;
  background: $color-light;
  color: $color-text-inactive;
}

.delivery__field_comment {
  max-width: 100%;
  min-width: 100%;
  min-height: 112px;
}
