.pickup__container {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 15px;
}

.pickup__label {
  display: flex;
  flex-grow: 2;
  align-items: center;
  min-width: min-content;
  padding: 0 15px 15px 20px;
  font-size: 1rem;

  span {
    padding-left: 0.5rem;
  }

  @media #{$tablet} {
    padding: 0 15px 15px 0;
  }
}

.pickup__map {
  height: 75vh;
  margin-bottom: 30px;
}