.order-form {
  width: 100%;
  max-width: 1032px;
  margin: 0 auto;
  background: $color-body;
}

.order-form__title {
  font-size: 2rem;
  font-style: normal;
  color: $color-title;
}

.order-form__inner-container {
  width: 100%;
  margin-top: 100px;
  padding: 0 0 45px;
  background: $color-light;
}

.order-form__block {
  display: none;

  &_delivery {
    padding: 0 40px 10px;
  }

  &_pickup {
    padding: 0 0 40px;

    @media #{$tablet} {
      padding: 0 40px 40px;
    }
  }
}

.order-form__input {
  display: none;
}

.order-form__tab {
  display: inline-block;
  max-width: 49%;
  padding: 0.9em 35px;
  text-align: center;
  background: $color-inactive;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  color: $color-text-inactive;
  font-size: 1rem;
  transform: translateY(-48px);
}

.order-form__input:checked + .order-form__tab {
  background: $color-light;
  color: $color-active;
}

.order-form__submit {
  display: block;
  margin: 0 auto;

  @media #{$tablet} {
    margin: 0 40px 0 auto;
  }
}

.order-form__input_delivery:checked ~ .order-form__block_delivery {
  display: flex;
  flex-direction: column;
}

.order-form__input_pickup:checked ~ .order-form__block_pickup {
  display: flex;
  flex-direction: column;
}
