body {
  width: auto;
  padding: 0;
  margin: 0;
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: normal;
  background: $color-body;
}

body * {
  box-sizing: border-box;
}

input,
label,
h1,
h2,
h3,
button,
ul,
li,
a {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  border: none;
}

#map {
  max-width: 952px;
  padding: 0;
  margin: 0;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  padding: 8vh 4vw;//72px 84px;
  margin: 0 auto;
}
